
import {setTitle} from './../../assets/js/common.js'
import { Button } from 'mint-ui';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
  name: 'index',
  data (){
    return {
      code:'', // 001206
      show_content:false,
      re_code_text:'',
      re_code_time:0,
      re_code_time_start:false,
      toast_show: false,
      user:'', // 
      toast_text:''
    }
  },
  created(){
    var store_arr=[
    'user',
    ];
    for(var i in store_arr){
      this[store_arr[i]]=this.$store.state[store_arr[i]];
    };
  },
  components:{
    'mtButton':Button,
    Group, XInput, Countdown, Toast
  },
  watch:{
    '$route':function(){
      console.log(this.$route.path=='/app/login')
    }
  },
  methods:{
    next_fn(){
      if(!this.user.realname.replace(/\s+/,'')){
        this.toast_text='';
        this.toast_show=true;
        return
      }
      if(!(/^1(3|4|5|7|8)\d{9}$/.test(this.user.phone))){
        this.toast_text='';
        this.toast_show=true;
        return
      }
      if(this.code!='1234'){
        this.toast_text='';
        this.toast_show=true;
        return
      }
      console.log('');

      this.$store.commit('user_up',this.user);
      console.log(this.$store.state.user);

      this.$router.push({name:'login_step2'})
    },
    re_code_fn(){
      if( this.re_code_time_start ) { // 
        console.log('')
        return
      }else{ // 
        console.log('');
        this.re_code_time=60;
        this.re_code_time_start=true;
      }
    },
    re_code_time_end_fn(){
      this.re_code_time_start=false;
    },
    protocol_fn(){
      this.show_content=!this.show_content;
      this.$router.push({path:'/app/login/protocol'});
    }
  }
}




